.User {
  margin-bottom: 50px;
}

.User .title {
  position: relative;
  height: 50vmin;
}

.User .title img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: center;
     object-position: center;
}

.User .title h2 {
  color: #fff;
  font-size: clamp(30px, 8vw, 55px);
  padding-top: 5vw;
  font-weight: 900;
  position: absolute;
}

.User .row .table_of_contents {
  background-color: #eee;
  padding: clamp(5px, 2vw, 15px);
  margin: clamp(5px, 2vw, 10px);
  -webkit-box-flex: 0;
      -ms-flex: 0 1 25%;
          flex: 0 1 25%;
}

.User .row .content {
  -webkit-box-flex: 0;
      -ms-flex: 0 1 75%;
          flex: 0 1 75%;
  margin: 10px;
}

.User .row .content h2 {
  font-size: clamp(25px, 7vw, 45px);
  font-weight: 900;
  color: #ff6f2c;
  margin-bottom: 20px;
}

.User .row .content h3 {
  font-weight: 900;
  font-size: 20px;
  margin: clamp(10px, 3vw, 20px) 0;
  -webkit-box-flex: 0;
      -ms-flex: 0 1 100%;
          flex: 0 1 100%;
}

.User .row .content p {
  padding: clamp(5px, 3vw, 20px) 0;
  line-height: 1.8;
}

.User .row .content .row {
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.User .row .content .row ol li,
.User .row .content ol li {
  padding: 5px 0;
  margin-left: 30px;
  list-style: decimal;
  font-weight: 600;
}

.User .row .content .row ol li::marker,
.User .row .content ol li::marker {
  color: #ff6f2c;
  font-size: 20px;
  font-weight: 900;
}

@media (max-width: 600px) {
  .User .row {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
  }
}
