.bike_list {
  padding-left: 40px;
  margin-bottom: 10px;
}

.bike_list .block_sort {
  -webkit-box-flex: 0;
      -ms-flex: 0 1 100%;
          flex: 0 1 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-transition: 0.5s ease;
  transition: 0.5s ease;
  overflow: hidden;
}

.bike_list .block_sort.active {
  max-height: 200px;
}

.bike_list .block_sort span {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}

.bike_list .block_sort .sort {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.bike_list .block_sort .sort label {
  cursor: pointer;
  -webkit-transition: 0.2s ease;
  transition: 0.2s ease;
  margin: 0 10px;
}

.bike_list .block_sort .sort label:hover {
  color: #ff6f2c;
}

.bike_list .block_sort .sort label input {
  display: none;
}

.bike_list .block_sort .btn_sort {
  margin-left: 5px;
  cursor: pointer;
  -webkit-transition: 0.2s ease;
  transition: 0.2s ease;
}

.bike_list .block_sort .btn_sort:hover {
  color: #ff6f2c;
}

.bike_list .bike_items {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 0 -40px;
}

@media (max-width: 1000px) {
  .bike_list {
    padding-left: 20px;
  }
  .bike_list .bike_items {
    margin: 0;
  }
}

@media (max-width: 860px) {
  .bike_list {
    padding: 0;
  }
  .bike_list .block_sort {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    max-height: 0;
    background-color: #f7f7f7;
    padding: 0 10px;
  }
  .bike_list .block_sort * {
    padding: 5px;
    font-weight: 900;
  }
  .bike_list .block_sort .sort {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    -webkit-box-align: start;
        -ms-flex-align: start;
            align-items: flex-start;
  }
  .bike_list .block_sort .sort label {
    margin: 0;
  }
}
